"M26.008 19.48c.778 0 1.408.63 1.408 1.408v7.424a1.408 1.408 0 1 1-2.816 0v-7.424c0-.778.63-1.408 1.408-1.408z"></path></g></svg>
          附近店家</p>
      <ul>
        <li @click="shop_tiao(item)" v-for="item of list">
            <div class="zuo">
              <img :src="'//elm.cangdu.org/img/'+item.image_path" alt="">
            </div>
            <div>
                <p class="top">
                  <span class="pai">品牌</span>
                  <span class="pai7"> {{item.name}}</span>
                  <span class="pai2">保准票</span>
                 </p>
                 <p>
                   <div class="block" style="display:inline-block">
</div>
<div class="di" style="margin-top:-0.2rem">
    <Star :num=item.rating></Star>
                     <span  class="cen">{{item.rating}}</span>
                  
  <span class="t">月售{{item.recent_order_num}}单</span>
  <span class="t1">鸵鸟专送</span>
  <span class="t2">准时达</span>
</div>

                 <div class="di">
                 <span class="pai3  pai5"> ￥20元起/{{item.piecewise_agent_fee.tips}}</span>
                 <span class="pai3">{{item.distance}}/</span>
                 <span class="pai4">{{item.order_lead_time}}</span>
                 </div>
            </div>
        </li>
      </ul>
  </div>
 
  </div>
</template>

<script>
import Star from "./star"
export default {
  data () {
    return {
    };
  },

  components: {Star},

  computed: {},

  mounted(){},

  created(){},

  methods: {
    
       shop_tiao(item){
         console.log(item)
      this.$router.push({
        name:"Shop",
        query:{
          id:item.id,
          name:item.name
        }
      })
    },
  },
  props:["list"]
}

</script>
<style scoped lang="scss">

.list{
  
  ul{
 
    li{display: flex;
    position: relative;
     background: white;
    margin-bottom: 0.02rem;
    height: 1.2rem;
    padding-bottom:0.3rem;
    font-size: 0.2rem;
list-style: none;
.t2{
  border:1px solid  #3190e8;
  color: #3190e8
}
.zuo{
  margin-right: 0.2rem;
  width: 20%;
  img{width:80%;margin: 0 auto;display: block}
}
}
  }
}
.pai{
  background:#ff9a0d;
  padding: 0.02rem 0.03rem;
  border-radius: 0.05rem

}
.pai2{
    position: absolute;
    right: 0.1rem;
  color: #ccc;
  font-size: 0.2rem;
}
.top{line-height: 1;display: flex; span{display: block}}
.pai3{
  color: #ccc;
 float: left;
}
.pai4{
  color: #3190e8;
}
.cen{
  line-height: 0.4rem;
   margin-left: 1rem;
}
.t{
   
  margin-right: 0.3rem;
   color: #ccc;
}
.pai5{
  margin-right: 0.3rem;
  font-size: 0.16rem;
}
.cen{
   color: #ff9a0d;
}
.t1{
    // float: right;
  color: white;
  background: #3190e8;

}
.list1{
  line-height: 0.6rem;
  background: white;
  margin-top: 0.2rem;
  color: #999;
  svg{
          fill: #999;
          width: 0.2rem;
          height: 0.2rem;
  }
}
.pai7{
 width: 3rem;
 overflow: hidden;
 display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
</style>


