<!--  -->

<template>
  <div  class='content'>
<Zi  goback="true"   :con=val>
        <router-link to="/search"  tag="h1"  slot="left">
       <svg style="margin:0.4rem 0;font-size:0.5rem"  data-v-070ab150="" width="200%" height="200%" xmlns="http://www.w3.org/2000/svg" version="1.1"><circle data-v-070ab150="" cx="8" cy="8" r="7" stroke="rgb(255,255,255)" stroke-width="1" fill="none"></circle> <line data-v-070ab150="" x1="14" y1="14" x2="20" y2="20" style="stroke: rgb(255, 255, 255); stroke-width: 2;"></line></svg>
      </router-link>
    </Zi>


<van-pull-refresh v-model="isLoading" @refresh="onRefresh"
pulling-text="正在刷新"
>



    <div  class="list2">

<van-list
  v-model="loading"
  :finished="finished"
  finished-text="没有更多了"
  @load="onLoad"
  loading="玩命加载中"
>

  <router-view></router-view>
  <div class="img"  :style="{'display':img==true?'block':'none'}">
          <img src="../../img/landing1.gif" alt="">
      </div>
 <List :list=list2></List>
 </van-list>

  <div class="mao" v-if="btnFlag"  @click="backTop">
    <i class="iconfont icon-top"></i>
   <p> 顶部</p>
  </div>
  
  </div>
 </van-pull-refresh>
       <Foot></Foot>
    </div>
</template>

<script>
import Vue from 'vue';
import { PullRefresh } from 'vant';

Vue.use(PullRefresh);
import axios from "axios" 
import Zi from "../../components/header"
import Foot from "../../components/foot"
import List from "../../components/shopList"
export default {
   name: 'scroll',
  data () {
    return {
          list: [],
          loading: false,
          finished: false,
          a:0,
          val:"",
          str:'',
          list:[],
          list2:[],
          val1:"",
          btnFlag:false,
          latitude:"",
          longitude:"",
        isLoading: false,
      img:true
    };
  },

  components: {Zi,Foot,List},

  computed: {
  
  },
destroyed () {
  window.removeEventListener('scroll', this.scrollTop)
},
beforeMount(){
  if(localStorage.getItem("nameList")){
      this.val=JSON.parse(localStorage.getItem("nameList")).name
      this.latitude=JSON.parse(localStorage.getItem("nameList")).latitude
      this.longitude=JSON.parse(localStorage.getItem("nameList")).longitude
    }
       axios.get("http://elm.cangdu.org/shopping/restaurants?latitude="+this.latitude+"&longitude="+this.longitude+"&offset=0&limit=30&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=").then((d)=>{
      this.list2=d.data
        this.img=false
    })

},
  mounted(){
 
    console.log(this.$route.params.name)
      this.img=false
    window.addEventListener('scroll', this.scrollTop)
    
     if(localStorage.getItem("nameList")){
     this.val1=JSON.parse(localStorage.getItem("nameList")).geohash
    }
    var that=this
    axios.get("http://elm.cangdu.org/v2/index_entry?geohash="+this.val1+"&group_type=1&flags[]=F").then((d)=>{
      console.log(d)
    
      if(d.data.length>0){
       
           this.list=d.data
      }
      console.log(this.img)
       
    })  
   
  
  },
mounted(){
 
},

  methods: {
     onRefresh() {
      setTimeout(() => {
        this.$toast('刷新成功');
        this.isLoading = false;
        this.count++;
      }, 500);
    },
      onLoad() {
    // 异步更新数据
        // 加载状态结束
        this.loading = false;
        this.a+=20
        axios.get("http://elm.cangdu.org/shopping/restaurants?latitude="+this.latitude+"&longitude="+this.longitude+"&offset="+this.a+"&limit=30&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=").then((d)=>{
            this.list2=this.list2.concat(d.data)
          })
    },
    shop_tiao(item){
      this.$router.push({
        name:"Shop",
        params:{
          id:item.id
        }
      })
    },
    backTop 
    () {
      var that = this
      let timer = setInterval(() => {
        let ispeed = Math.floor(-that.scrollTop / 5)
        document.documentElement.scrollTop = document.body.scrollTop = that.scrollTop + ispeed
        if (that.scrollTop === 0) {
          clearInterval(timer)
        }
      }, 16)

  },
  }
}
</script>
<style scoped lang="scss">
.list2{
  overflow: scroll;
  overflow: hidden;
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
  height: 70%;
}
.mao{
  z-index: 5;
  p{
    text-align: center;
    margin: 0 ;padding: 0
  }
position: fixed;
right: 0;
top: 30%;
width: 1rem;height:1rem;border:0.01rem solid #ccc;
border-radius: 50%;
text-align: center;
// line-height: 1rem;
  .icon-top{
  font-size: 0.4rem;
  line-height: 0.6rem;
  z-index: 100;
}
}

.iconfont{
  font-size: 30px;
}
.list{
  ul{
 
    li{display: flex;
     background: white;
    margin-bottom: 0.02rem;
    height: 1.2rem;
    paddind-bottom:0.3rem;
    font-size: 0.2rem;
list-style: none;
.zuo{
  margin-right: 0.2rem;
  width: 20%;
  img{width:80%;margin: 0 auto;display: block}
}
}
  }
}
.pai{
  background:#ff9a0d;
  padding: 0.02rem 0.03rem;
  border-radius: 0.05rem

}
.pai2{
  color: #ccc;
  font-size: 0.2rem;
 float: right;
}
.top{line-height: 1;display: flex; span{display: block}}
.pai3{
  color: #ccc;
 float: left;
}
.pai4{
  color: #3190e8;
}
.cen{
  line-height: 0.4rem
}
.t{
  margin-right: 0.3rem;
   color: #ccc;
}
.pai5{
  margin-right: 0.3rem;
  font-size: 0.16rem;
}
.cen{
   color: #ff9a0d;
}
.t1{
  color: white;
  background: #3190e8;

}
.t2{
  border:1px solid  #3190e8;
  color: #3190e8
}

.zuo1,.you1{
  width: 0.5rem;
  display: block;
  margin-top: 0.3rem
}
.zuo1{float: left}
.you1{float: right}
.list1{
  line-height: 0.6rem;
  background: white;
  margin-top: 0.2rem;
}
.di{
  font-size: 0.16rem;
}
.pai7{
 width: 3rem;
 overflow: hidden;
 display: block;

}
.van-list{
  ul{
    height: 80%;
  }
}
.content{
  display: flex;
  flex-direction: column;
  height: 100%;
}
.img{
 img{
    display: block;
  margin:auto;
 }
}
</style>


